<template>
    <div id="wrap">
        <div id="header">
            <div id="shower">
                {{top}}
            </div>
            <div id="calvalue" v-bind:style="{fontSize:fontSize+ 'px'}">
                {{bottom}}
            </div>
        </div>
        <div id="body">
            <div v-for="row in keyboard">
                <div v-for="cell in row">
                    <div  :class="{equal:`${cell.value}`=='equal'}"  @click="cellClick(cell.type,cell.value,cell.title)"><span>{{cell.title}}</span></div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    name:"calculate",
    data(){
        return {
            leftval:"",
            rightval:"",
            operator:"",
            input:"0",
            keyboard:[
                [
                    {
                        title:"C",
                        value:"clear",
                        type:"operator"
                    },
                    {
                        title:"DEL",
                        value:"del",
                        type:"operator"
                    },
                    {
                        title:"%",
                        value:"perc",
                        type:"operator"
                    },
                    {
                        title:"/",
                        value:"divide",
                        type:"operator"
                    }
                ],
                [
                    {
                        title:"7",
                        value:"7",
                        type:"num"
                    },
                    {
                        title:"8",
                        value:"8",
                        type:"num"
                    },
                    {
                        title:"9",
                        value:"9",
                        type:"num"
                    },
                    {
                        title:"*",
                        value:"multi",
                        type:"operator"
                    }
                ],
                [
                    {
                        title:"4",
                        value:"4",
                        type:"num"
                    },
                    {
                        title:"5",
                        value:"5",
                        type:"num"
                    },
                    {
                        title:"6",
                        value:"6",
                        type:"num"
                    },
                    {
                        title:"-",
                        value:"minus",
                        type:"operator"
                    }
                ],
                [
                    {
                        title:"1",
                        value:"1",
                        type:"num"
                    },
                    {
                        title:"2",
                        value:"2",
                        type:"num"
                    },
                    {
                        title:"3",
                        value:"3",
                        type:"num"
                    },
                    {
                        title:"+",
                        value:"plus",
                        type:"operator"
                    }
                ],
                [
                    {
                        title:"00",
                        value:"00",
                        type:"num"
                    },
                    {
                        title:"0",
                        value:"0",
                        type:"num"
                    },
                    {
                        title:".",
                        value:"point",
                        type:"operator"
                    },
                    {
                        title:"=",
                        value:"equal",
                        type:"operator"
                    }
                ]
            ]          
        }
    },
    computed:{       
        top(){
            return (this.leftval.indexOf('.')<0?this.leftval:parseFloat((this.leftval*1).toFixed(6)).toString()) + this.operator +this.rightval
        },
        bottom(){
           return this.input.indexOf('.')<0?
            this.input:
            this.input.indexOf('.')==this.input.length-1 ? 
            this.input:
            parseFloat((this.input*1).toFixed(6)).toString()             
        },
        fontSize(){
            var len =this.bottom.length-7;
            return len>0?50-len*3:50;
        }
    },
    methods:{
        cellClick(type,value,title){
            switch(type){
                case "operator":
                    this.dooperator(value,title);
                    break;
                case "num":
                    this.addnum(value);
                    break;
            }
        },
       
        addnum(value){
            if(this.input.length==10) return false;
            if(this.input[0]=="0"&&value==0) return false
            if(this.rightval!=""){
                this.dooperator('clear');
                this.input=value;
                return false
            }
            this.input=(this.input==="0"?"":this.input) + value;
        },
        dooperator(op,title){
            switch(op){
                case 'clear':
                    this.leftval="";
                    this.rightval="";
                    this.operator="";
                    this.input="0";
                break;
                case 'del':
                    if(this.rightval!="") return false;
                    let val = this.input.slice(0,this.input.length-1)
                    this.input=val?val:"0";
                break;
                case 'plus':                   
                case 'minus':
                case 'multi':
                case 'divide':
                    this.cal(title)
                    break;  
                case 'perc':
                    this.leftval=this.input;
                    this.operator=""
                    this.rightval="% =";
                    this.input=(this.input/100).toString()
                    break;
                case 'point':
                    if(this.input.indexOf(".")<0){
                        this.input+="."
                    }
                    break;
                case 'equal':
                    let result="";
                    if(this.leftval==""){
                        this.leftval=this.input;
                        this.rightval="=";
                        result=this.input;
                    }else{
                        if(this.rightval==""){
                            result = eval(this.leftval*1 + this.operator  + this.input*1);
                            this.rightval=this.input+"=";
                        }else{
                            this.leftval=this.input
                            result=   eval(this.input*1 + this.operator +this.rightval.replace('=','') );
                        }
                    }
                    
                    this.input = result.toString()
                    break        
            }
        },
        cal(title){
            if(this.leftval!=""&&this.operator!=""&&this.input!=""&&this.rightval==""){
                this.leftval=eval(this.leftval*1 + this.operator  + this.input*1);
                this.operator=title;
                this.rightval=""
                this.input="";
                return false;
            }
            this.operator = title;
            this.leftval=this.input;
            this.input="";
            this.rightval=""
        }
    }
}
</script>
<style scoped>
    #wrap{
        max-width: 320px;
        height: 500px;
        border: 1px solid #ccc;
        background: #f8f8f8;
        margin: 15px auto;
        color:darkgray ;
        font-family:Verdana, Geneva, Tahoma, sans-serif
    }
    #header{
        height: 110px;
        border-bottom:1px solid #ddd;
        text-align: right;
        
        padding: 20px;
        margin: 0 20px;
    }
    #shower{
        font-size: 20px;
        height: 40px;
        line-height: 40px

    }
    #calvalue{
        color: dimgrey;
    }
    #body{
        padding: 15px 0;
        display: flex;
        flex-direction: column;
        -webkit-user-select:none;
        -moz-user-select:none;
        -ms-user-select:none;
        user-select:none;
        font-size: 23px;
    }
     #body>div{
         flex: 1;
         display: flex;
         margin: 6px 0;
         padding: 10px;
     }
    #body>div>div{
        flex: 1;
        cursor: pointer;
    }
    #body>div>div:hover{
        color: burlywood
    }
    .equal span{
        height: 35px;
        width: 35px;
        line-height: 35px;
        background: #e5817e;
        color: #fff;
        border-radius: 50%;
        display: inline-block;
        box-shadow: 1px 3px 11px 0px #e5a2a0;
    }
</style>



